body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ry-dropdown {
  height: 100px;
  position: relative;
  top: 40px;
  flex-shrink: 0; }
  .ry-dropdown .ry-dropdown-title {
    margin: 0 0 0 25px;
    cursor: pointer;
    color: rgba(0, 0, 0, 0.65); }
  @media (max-width: 770px) {
    .ry-dropdown .ry-dropdown-title {
      margin: 0 0 0 12px; } }
  .ry-dropdown .ry-dropdown-list {
    position: absolute;
    top: 65px;
    box-shadow: 0 5px 35px rgba(32, 30, 30, 0.1);
    background-color: rgba(247, 245, 245, 0.7);
    border-radius: 6px;
    width: 140px;
    display: flex;
    flex-direction: column;
    -webkit-transition: all 0.35s;
    transition: all 0.35s; }
    .ry-dropdown .ry-dropdown-list .ry-dpd-item {
      color: rgba(0, 0, 0, 0.65);
      flex-wrap: nowrap;
      height: 45px;
      line-height: 45px;
      width: 100%;
      text-align: center;
      border-radius: 6px; }
    .ry-dropdown .ry-dropdown-list .ry-dpd-item:hover {
      background-color: rgba(247, 245, 245, 0.25); }

.ry-header {
  z-index: 999;
  position: absolute;
  left: 60px;
  top: 50px;
  right: 60px;
  min-width: 600px;
  background-color: rgba(247, 245, 245, 0.7);
  border-radius: 6px;
  height: 70px;
  box-shadow: 0 5px 35px rgba(32, 30, 30, 0.1);
  display: flex;
  align-items: center;
  justify-content: space-between;
  -webkit-transition: all 0.35s;
  transition: all 0.35s; }
  .ry-header .ry-header-icon {
    margin-left: 60px; }
    .ry-header .ry-header-icon img {
      position: relative;
      top: 1px;
      height: 30px; }
  .ry-header .ry-header-ct {
    margin: 0 140px 0 80px;
    display: flex;
    flex-grow: 1;
    justify-content: space-between; }
  .ry-header .ry-header-right {
    margin-right: 80px;
    color: #9e2929;
    font-weight: bold;
    flex-shrink: 0; }
  @media (max-width: 850px) {
    .ry-header .ry-header-ct {
      margin-left: 0; } }
  @media (max-width: 1050px) {
    .ry-header .ry-header-right {
      display: none; }
    .ry-header .ry-header-ct {
      margin-left: 10px; }
    .ry-header .ry-header-icon {
      margin-left: 30px; } }

.ry-header__top {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border-radius: 0px;
  height: 85px; }

.ry-footer {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .ry-footer .ry-footer-top {
    width: 100%;
    padding: 40px 25% 30px 25%;
    display: flex;
    justify-content: space-between; }
    .ry-footer .ry-footer-top .ry-footer-item {
      text-align: center; }
      .ry-footer .ry-footer-top .ry-footer-item .item-title {
        font-size: 16px;
        margin: 14px 5px;
        font-weight: bold; }
      .ry-footer .ry-footer-top .ry-footer-item .item-cnt {
        font-size: 12px;
        margin: 5px 0; }
  .ry-footer .ry-footer-mid {
    margin: 30px 0 15px 0;
    font-weight: 800;
    font-size: 18px; }
    .ry-footer .ry-footer-mid .ry-footer-mid-text {
      display: flex;
      padding: 12px 18px 4px 18px;
      justify-content: space-between;
      align-items: center; }
  .ry-footer .ry-footer-bottom {
    margin: 40px 0 120px 0;
    font-size: 14px; }

body {
  min-width: 1000px;
}

.ry-body {
  min-height: 600px;
}

